.Home{
    padding:1cm;
    width:100vw;
    height:95vh;
    background-image: url('background.jpg');
    background-size: cover;
    color: rgb(240, 240, 240);
    font-size: 20px;
    font-weight: bold;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.heading{
    font-size: 25px;
    text-shadow: 2px 6px black;
}

.searchBar{
    padding: 0.5cm;
    border-radius: 20px;
    font-size: 16px;
    border: none;
}

.temp{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
}  

.min, .max{
    display: flex;
    flex-direction: row;
}  

.icon{
    width:1cm;
    height:1cm;
}

.row1{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    justify-content: center;
    align-items: center;
}
.row2{
    display: flex;
    flex-direction: row;
    column-gap: 1cm;
    justify-content: center;
    align-items: center;
}